import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { withFirebase } from "../firebase";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import SocialButton from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import "./AuthPage.scss";
import SvgGoingUp from "../svg/going-up-animate.svg";
import {
  action_sign_in,
  action_sign_up,
  thunk_user_signin,
  thunk_user_signup,
} from "../redux/actions/authActions";
import { connect } from "react-redux";
import SetAuthorizationToken from "../utils/SetAuthorizationToken";
import { action_dialog_open } from "../redux/actions/dialogAction";
import ProgressBar from "../components/ProgressBar";
import {
  action_progress_start,
  action_progress_stop,
} from "../redux/actions/progressAction"
import MyDialog from "../components/MyDialog";
import OnorLogo from "../images/onorlogo.png";
import loginImage from "../images/login.png";
import joinImage from "../images/join.png";
import FacebookIcon from "@material-ui/icons/Facebook";
import AppleIcon from "@material-ui/icons/Apple";
import GTranslateIcon from "@material-ui/icons/GTranslate";
import { isUndefined } from "lodash";

let AuthPage = (props) => {


  const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  }));

  const {
    thunk_user_signup,
    thunk_user_signin,
    action_sign_in,
    action_sign_up,
    signinPage,
    signupPage,
    auth,
  } = props;
  // const {auth} = props

  const [name, setName] = useState("");
  const [lname, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState(0);
  const [password, setPassword] = useState("");
  const [rePassword, setRePassword] = useState("");
  const handleSignUp = async (e) => {
    if (validateSignup()) {
      e.preventDefault();
      props.actionStartProgress();
      // let data = new FormData()
      // data.append('first_name', name)
      // data.append('last_name', lname)
      // data.append('email', email)
      // data.append('phone', phone)
      // data.append('password', password)
      // data.append('avatar', new File([""], ""))
      let data = {
        'first_name': name,
        'last_name': lname,
        'email': email,
        'phone': phone,
        'password': password
      }
      try {
        const result = await thunk_user_signup(data);
        console.log(props.firebase);
        // props.firebase
        //   .doCreateUserWithEmailAndPassword(data.email, data.password)
        //   .then((fbResult) => {
        //     console.log("FIREBASE SIGNUP RESULT");
        //     console.log({ ...fbResult });
        //   })
        //   .catch((e) => {
        //     console.log("FIREBASE SIGNUP ERROR");
        //     console.log({ ...e });
        //   });

        console.log("SPRING_SIGNUP_RESULT");
        console.log({ ...result });
        props.actionStopProgress();
        const token = result.data.token;
        localStorage.setItem("jwtToken", token);
        SetAuthorizationToken(token);
        action_sign_up(token);
        props.actionDialogOpen({
          title: "Registration Successful",
          message:
            "You are successfully registered.",
          positive: "Close",
          type: "Alert",
        });
        props.history.push("/");
      } catch (e) {
        console.log(e);
        console.log({ ...e });
        props.actionStopProgress();
        if (!isUndefined(e) && !isUndefined(e.response)) {
          if (e.response.status === 401) {
            props.actionDialogOpen({
              title: `Registration failed.`,
              message: `${e.response.data.message}. Server returned error of type: ${e.response.data.error}`,
              positive: "Ok",
              negative: "Close",
              type: "Alert",
            });
          } else {
            props.actionDialogOpen({
              title: `Registration failed.`,
              message: `Server returned error with response code: ${e.response.status}.

                        Something went wrong. Couldn't register currently. Please try again`,
              positive: "Ok",
              negative: "Close",
              type: "Alert",
            });
          }
        }

        //   localStorage.removeItem("jwtToken");
        //   SetAuthorizationToken();
      }
    }
  };

  const [signinEmail, setSigninEmail] = useState("");
  const [signinEmailError, setSigninEmailError] = useState("");
  const [signinPassword, setSigninPassword] = useState("");
  const [signinPasswordError, setSigninPasswordError] = useState("");

  const [registrationValidation, registrationValidationError] = useState({
    name: '',
    lname: '',
    email: '',
    phone: '',
    password: '',
    rePassword: ''
  })

  const validateLogin = () => {
    if (signinEmail === '') {
      setSigninEmailError('Email is required')
      setSigninPasswordError('')
      return false
    } else if (!signinEmail.includes('@')) {
      setSigninPasswordError('')
      setSigninEmailError('Invalid Email')
      return false
    } else if (signinPassword === '') {
      setSigninEmailError('')
      setSigninPasswordError('Password is required')
      return false
    } else {
      return true
    }
  }

  const validateSignup = () => {
    if (name === '') {
      registrationValidationError({
        name: "First Name is required",
        lname: '',
        email: '',
        phone: '',
        password: '',
        rePassword: ''
      })
      return false
    } else if (lname === '') {
      registrationValidationError({
        lname: "Last Name is required",
        name: '',
        email: '',
        phone: '',
        password: '',
        rePassword: ''
      })
      return false
    } else if (email === '') {
      registrationValidationError({
        email: "Email is required",
        name: "",
        lname: '',
        phone: '',
        password: '',
        rePassword: ''
      })
      return false
    } else if (!email.includes('@')) {
      registrationValidationError({
        email: "Invalid Email",
        name: "",
        phone: '',
        password: '',
        lname: '',
        rePassword: ''
      })
      return false
    } else if (phone === '') {
      registrationValidationError({
        phone: "Phone number is required",
        name: "",
        email: '',
        password: '',
        lname: '',
        rePassword: ''
      })
      return false
    } else if (phone.length !== 10) {
      registrationValidationError({
        phone: "A phone number should contain 10 digits",
        name: "",
        email: '',
        password: '',
        lname: '',
        rePassword: ''
      })
      return false
    } else if (isNaN(phone)) {
      registrationValidationError({
        phone: "Invalid Phone",
        name: "",
        email: '',
        lname: '',
        password: '',
        rePassword: ''
      })
      return false
    } else if (password === '') {
      registrationValidationError({
        password: "Password is Required",
        name: "",
        phone: '',
        lname: '',
        email: '',
        rePassword: ''
      })
      return false
    } else if (!password.match(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/)) {
      registrationValidationError({
        password: "Password must have at least a number and a special character with 6-16 characters in total",
        name: "",
        phone: '',
        lname: '',
        email: '',
        rePassword: ''
      })
    } else if (rePassword === '') {
      registrationValidationError({
        rePassword: "Please confirm your entered password",
        name: "",
        lname: '',
        phone: '',
        password: '',
        email: ''
      })
      return false
    } else if (rePassword !== password) {
      registrationValidationError({
        rePassword: "Passwords doesn't match",
        name: "",
        lname: '',
        phone: '',
        password: '',
        email: ''
      })
      return false
    } else {
      registrationValidationError({
        rePassword: '',
        name: '',
        lname: '',
        phone: '',
        password: '',
        email: ''
      })
      return true
    }
  }

  const handleSignIn = async (e) => {
    if (validateLogin()) {
      setSigninEmailError('')
      setSigninPasswordError('')
      props.actionStartProgress();
      e.preventDefault();
      const data = {
        email: signinEmail,
        password: signinPassword,
      };
      console.log('SIGNIN DATA')
      console.log({ ...data })
      try {
        const result = await thunk_user_signin(data);
        props.actionStopProgress();
        const token = result.data.token;
        localStorage.setItem("jwtToken", token);
        SetAuthorizationToken(token);
        action_sign_in(token);
        // props.actionDialogOpen({
        //   title: "Success",
        //   message:
        //     "You are successfully Logged in",
        //   positive: "Close",
        //   type: "Alert",
        // });
        props.history.push("/");
      } catch (e) {
        console.log({ ...e });
        props.actionStopProgress();
        props.actionDialogOpen({
          title: "Sign in failed",
          message: `${e.response.data.message}`,
          positive: "Retry",
          type: "Alert",
        });

        //   localStorage.removeItem("jwtToken");
        //   SetAuthorizationToken();
      }
    }
  };

  const static_side_jsx = (
    <Fragment>
      <Col md={6} sm={12} className="d-none d-md-block mycol">
        <img src={SvgGoingUp} alt={"svg going up"} />
      </Col>
    </Fragment>
  );
  const classes = useStyles();
  const sign_up_jsx = (
    <React.Fragment>
      <Row style={{ height: "500px" }}>
        <Col sm={6} className={"imageDiv"}>
          <img
            src={OnorLogo}
            alt="logo"
            className={
              "sideImg img-responsive img-fluid d-flex justify-content-center"
            }
            style={{
              height: "180px",
              width: "300px",
              marginTop: "15em",
              marginLeft: "6em",
            }}
          />
        </Col>
        <Col
          xs={12}
          sm={12}
          md={6}
          lg={6}
          className={"content d-flex justify-content-center"}
        >
          <div className="form-group row">
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <img
                src={OnorLogo}
                alt="logo"
                className={
                  "img-responsive img-fluid d-flex justify-content-center"
                }
                style={{ height: "70px", width: "120px" }}
              />
            </div>
            <div className="col-sm-12 mt-3 mb-1 d-flex justify-content-center">
              <h5 className={"header_h4"}>JOIN</h5>
            </div>
            <div className="col-sm-12 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setName(e.target.value)}
                type="text"
                placeholder="Enter First Name"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{registrationValidation.name}</span>
            </div>


            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setLastName(e.target.value)}
                type="text"
                placeholder="Enter Last Name"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{registrationValidation.lname}</span>
            </div>
            {/* <div className="col-sm-12 mt-3 d-flex justify-content-center">
                            <Form.Control className={'w-75'} style={{ borderRadius: '10px' }} onChange={(e) => setLastName(e.target.value)} type="text" placeholder="Enter Last Name"/>
                        </div> */}
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setEmail(e.target.value)}
                type="email"
                placeholder="Enter email"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{registrationValidation.email}</span>
            </div>
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setPhone(e.target.value)}
                type="phone"
                placeholder="Enter phone number"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{registrationValidation.phone}</span>
            </div>
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setPassword(e.target.value)}
                type="password"
                placeholder="Password"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{registrationValidation.password}</span>
            </div>
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setRePassword(e.target.value)}
                type="password"
                placeholder="Re enter Password"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{registrationValidation.rePassword}</span>
            </div>
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <Form.Group controlId="formBasicCheckbox">
                <Form.Check type="checkbox" label="Check me out" />
              </Form.Group>
            </div>
            <div className="col-sm-12 col-xs-12 d-flex justify-content-center">
              <div className="col-sm-6 col-xs-6 d-flex justify-content-center">
                <Link to="/signin/">
                  <Button variant="primary" className={"onor_secondery_btn"}>
                    Back
                  </Button>
                </Link>
              </div>
              <div className="col-sm-6 col-xs-6 d-flex justify-content-center">
                <Button
                  onClick={handleSignUp}
                  variant="primary"
                  className={"onor_btn"}
                >
                  Join
                </Button>
              </div>
            </div>
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <p>OR</p>
            </div>
            <div className="col-sm-12 d-flex justify-content-center">
              <SocialButton
                variant="contained"
                style={{
                  backgroundColor: "#3b5998",
                  color: "white",
                  width: "20em",
                }}
                className={classes.button}
                startIcon={<FacebookIcon />}
              >
                Continue with Facebook
              </SocialButton>
            </div>
            <div className="col-sm-12 d-flex justify-content-center">
              <SocialButton
                variant="contained"
                style={{
                  backgroundColor: "#fff",
                  color: "black",
                  width: "20em",
                }}
                className={classes.button}
                startIcon={<GTranslateIcon />}
              >
                Continue with Google
              </SocialButton>
            </div>
            <div className="col-sm-12 d-flex justify-content-center">
              <SocialButton
                variant="contained"
                style={{
                  backgroundColor: "#fff",
                  color: "black",
                  width: "20em",
                }}
                className={classes.button}
                startIcon={<AppleIcon />}
              >
                Continue with Apple
              </SocialButton>
            </div>
          </div>
        </Col>
      </Row>
    </React.Fragment>
  );

  const sign_in_jsx = (
    <React.Fragment>
      <Row style={{ height: "500px" }}>
        <Col sm={6} className={"imageDiv"}>
          <img
            src={OnorLogo}
            alt="logo"
            className={
              "sideImg img-responsive img-fluid d-flex justify-content-center"
            }
            style={{
              height: "180px",
              width: "300px",
              marginTop: "12em",
              marginLeft: "6em",
            }}
          />
        </Col>
        <Col
          xs={12}
          sm={12}
          md={6}
          lg={6}
          className={"content d-flex justify-content-center"}
        >
          <div className="form-group row">
            <div className="col-sm-12 mt-3 d-flex justify-content-center">
              <img
                src={OnorLogo}
                alt="logo"
                className={
                  "img-responsive img-fluid d-flex justify-content-center"
                }
                style={{ height: "70px", width: "120px" }}
              />
            </div>
            <div className="col-sm-12 mt-3 mb-1 d-flex justify-content-center">
              <h5 className={"header_h4"}>LOGIN</h5>
            </div>
            <div className="col-sm-12 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setSigninEmail(e.target.value)}
                type="email"
                placeholder="Enter email"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <span style={{ color: 'red' }}>{signinEmailError}</span>
            </div>
            <div className="col-sm-12 mt-4 d-flex justify-content-center">
              <Form.Control
                className={"w-75"}
                style={{ borderRadius: "10px" }}
                onChange={(e) => setSigninPassword(e.target.value)}
                type="password"
                placeholder="Password"
              />
            </div>
            <div className="col-sm-12 mt-1 d-flex justify-content-center">
              <label style={{ color: 'red' }}>{signinPasswordError}</label>
            </div>
            <div className="col-sm-12 col-xs-12 d-flex justify-content-center">
              <div className="col-sm-6 mt-4 d-flex justify-content-center">
                <img
                  src={loginImage}
                  onClick={handleSignIn}
                  alt={"Login"}
                  style={{ height: "5em", width: "2.8em", cursor: "pointer" }}
                />
              </div>
              <div className="col-sm-6 mt-4 d-flex justify-content-center">
                <Link to="/signup/">
                  <img
                    src={joinImage}
                    alt={"Join"}
                    style={{ height: "5em", width: "2.8em", cursor: "pointer" }}
                  />
                </Link>
              </div>
            </div>
            <div className="col-sm-12 mt-4 d-flex justify-content-center">
              <span className={"onor_span_color"}>
                Not a member?
                </span>
            </div>
            <div className="col-sm-12 mt-4 d-flex justify-content-center">
              <a style={{ cursor: 'pointer' }} className={"onor_span_color"}>Need help?</a>
            </div>
            <div className="col-sm-12 mt-4 d-flex justify-content-center">
              <p style={{ color: "#252525", textAlign: "center" }}>
                By signing up you agree with our
                <br />
                <span style={{ fontSize: "15pt" }}>
                  Privacy Policy and Terms &amp; Conditions
                </span>
              </p>
            </div>
          </div>
        </Col>
      </Row>
    </React.Fragment>
  );

  const unauthorized_jsx = (
    <Fragment>
      <Container fluid className={"px-5"}>
        <Row>
          {/* {static_side_jsx} */}
          {signinPage && sign_in_jsx}
          {signupPage && sign_up_jsx}
        </Row>
      </Container>
    </Fragment>
  );

  const authorized_jsx = (
    <Fragment>
      <Container>
        <Row className={"d-flex justify-content-center"}>
          <Button className={"ml-5 mt-5"} variant="primary">
            Please Logout First
          </Button>
          {/* {
            props.history.push('/')
          } */}
        </Row>
      </Container>
    </Fragment>
  );

  return (
    <Fragment>
      {/* {props.dialog.isOpen && <MyDialog />} */}
      {/* {props.progress.isOpen && (
        <ProgressBar title={"Please wait while you are being logged in"} />
      )} */}
      {auth.isAuthorized ? authorized_jsx : unauthorized_jsx}
    </Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    progress: state.progress,
    dialog: state.dialog,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    thunk_user_signup: (data) => dispatch(thunk_user_signup(data)),
    thunk_user_signin: (data) => dispatch(thunk_user_signin(data)),
    action_sign_in: (data) => dispatch(action_sign_in(data)),
    action_sign_up: (data) => dispatch(action_sign_up(data)),
    actionDialogOpen: (payload) => dispatch(action_dialog_open(payload)),
    actionStartProgress: (payload) => dispatch(action_progress_start(payload)),
    actionStopProgress: () => dispatch(action_progress_stop()),
  };
};

AuthPage = connect(mapStateToProps, mapDispatchToProps)(AuthPage);
export default withFirebase(AuthPage);
