import React from "react";
import "./ServiceHeader.scss";
import GridImg from "../assets/grid.png";
import Button from "react-bootstrap/Button";
import { Link } from "react-router-dom";

function ServiceHeader(props) {
  return (
    <div className="service_parent mt-3">
      {/* <div>
            <div className="service_text vertical">Featured Sessions</div>
          </div> */}
      <img className="service_img" src={GridImg} alt="services"></img>

      <div className="service_add">
        {props.isRequest === 1 ? (
          <div className="service_body">
            <div className="service_text">Have a custom requirement?</div>
            <div className="service_text">We are here to help</div>

            <Button
              className="onor_outline_title_btn service_btn"
              as={Link}
              to="/buyer/postrequest"
            >
              Post a Request
            </Button>
          </div>
        ) : (
          <div className="service_body">
            <div className="service_text">
              Want to enter the world of freelancing?
            </div>
            <div className="service_text">We are here to help</div>
            <Button
              className="onor_outline_title_btn service_btn"
              as={Link}
              to="/seller/create/summary"
            >
              Post a Session
            </Button>
          </div>
        )}
      </div>
    </div>
  );
}

export default ServiceHeader;
