import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { API_FETCH_GIGS } from "../../utils/API_ENDPOINTS";
import withWidth, { isWidthUp } from "@material-ui/core/withWidth";
import { Row, Col, Container } from "react-bootstrap";
import { connect } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import GridListTileBar from "@material-ui/core/GridListTileBar";
import ListSubheader from "@material-ui/core/ListSubheader";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from "@material-ui/icons/Info";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import { isUndefined } from "lodash";
import { usePromiseTracker, trackPromise } from "react-promise-tracker";
import Loader from "react-loader-spinner";

const style = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "auto",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: "100%",
    height: "auto",
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
}));

let PopularGigsList = (props) => {
  const { promiseInProgress } = usePromiseTracker();
  const getGridListCols = () => {
    if (isWidthUp("xl", props.width)) {
      return 4;
    }

    if (isWidthUp("lg", props.width)) {
      return 4;
    }

    if (isWidthUp("md", props.width)) {
      return 3;
    }

    if (isWidthUp("sm", props.width)) {
      return 2;
    }

    if (isWidthUp("xs", props.width)) {
      return 2;
    }
  };

  const [productList, setProductList] = useState([]);
  const classes = style();
  useEffect(() => {
    handleGetProductList();
  }, []);
  const handleGetProductList = async () => {
    try {
      const prodResult = await trackPromise(axios.get(API_FETCH_GIGS));
      if (!isUndefined(prodResult) && !isUndefined(prodResult.data)) {
        setProductList(prodResult.data.gig_list.productList);
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <React.Fragment>
      <div className={"mt-5"}>
        <Container>
          <Row>
            <Col>
              <h5>POPULAR GIGS</h5>
            </Col>
            <Col>
              <p
                className={"float-right onor_span_color"}
                style={{ cursor: "pointer" }}
                onClick={() => props.history.push("/")}
              >
                Home
              </p>
            </Col>
          </Row>
        </Container>
      </div>
      <div className={"mt-1"}>
        {promiseInProgress && (
          <div
            style={{
              width: "100%",
              height: "100",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Loader type="ThreeDots" color="#fa6331" height="100" width="100" />
          </div>
        )}
        {!promiseInProgress && (
          <Container>
            <Row className={"d-flex justify-content-center"}>
              <Col sm={12}>
                <GridList className={classes.gridList} cols={getGridListCols()}>
                  {productList.map((currVal, index) => {
                    return (
                      <GridListTile
                        className="mx-4 my-3"
                        key={index}
                        style={{
                          boxShadow:
                            "0 4px 8px 0 rgba(0, 0, 0, 0.8), 0 6px 20px 0 rgba(0, 0, 0, 0.4)",
                        }}
                      >
                        <img
                          style={{ cursor: "pointer", objectFit: "contain" }}
                          title={currVal.productName}
                          onClick={(e) => {
                            props.history.push(
                              "/buyer/order/summary/" + currVal.productId + "/"
                            );
                          }}
                          src={currVal.productIconLink}
                          alt={index}
                        />
                        <GridListTileBar
                          title={currVal.productName}
                          subtitle={
                            <div>
                              <span>
                                By:{" "}
                                {currVal.merchant.firstName +
                                  " " +
                                  currVal.merchant.lastName}
                              </span>
                              <br />
                              <span>
                                Price: $
                                {currVal.price !== null ? currVal.price : 5000}
                              </span>
                            </div>
                          }
                          classes={{
                            root: classes.titleBar,
                            title: classes.title,
                          }}
                          actionIcon={
                            <IconButton aria-label={`star ${index}`}>
                              <StarBorderIcon className={classes.title} />
                            </IconButton>
                          }
                        />
                      </GridListTile>
                    );
                  })}
                </GridList>
              </Col>
            </Row>
          </Container>
        )}
      </div>
    </React.Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    dialog: state.dialog,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // get_homepage_gigs: () => dispatch(get_homepage_gigs()),
  };
};
PopularGigsList = withWidth()(PopularGigsList);
export default connect(mapStateToProps, mapDispatchToProps)(PopularGigsList);
